.drafter-frame-- {
    margin: 0 auto;
}

body {
    margin: 0;
}

.drafter-body-- {
    box-sizing: border-box;
    padding: 20px;
    font-size: 1.5em;
    line-height: 1.6;
    font-weight: 400;
    font-family: Raleway, HelveticaNeue, "Helvetica Neue", Helvetica, Arial, sans-serif;
